rid item\n */\n\n@mixin grid-item-spacing-left {\n  padding-left: $grid-item-spacing-s;\n\n  @include breakpoint($bp-m) {\n    padding-left: $grid-item-spacing-m;\n  }\n\n  @include breakpoint($bp-l) {\n    padding-left: $grid-item-spacing-l;\n  }\n}\n\n@mixin grid-item-spacing-right {\n  padding-right: $grid-item-spacing-s;\n\n  @include breakpoint($bp-m) {\n    padding-right: $grid-item-spacing-m;\n  }\n\n  @include breakpoint($bp-l) {\n    padding-right: $grid-item-spacing-l;\n  }\n}\n\n@mixin grid-item-spacing {\n  @include grid-item-spacing-left;\n  @include grid-item-spacing-right;\n}\n\n@mixin grid-item($columns) {\n  @include grid-item-spacing;\n  flex-grow: 1;\n  flex-shrink: 1;\n  flex-basis: auto;\n  width: grid-calc($columns);\n}\n\n@mixin grid-item-spaceless($columns) {\n  flex-grow: 1;\n  flex-shrink: 1;\n  flex-basis: grid-calc($columns);\n}\n\n/**\n * DEMO ONLY Grid\n */\n\n.demo-grid-overlay {\n  @include page-spacing;\n  max-width: #{$container-width + 80px};\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  height: 100%;\n  left: 50%;\n  transform: translateX(-50%);\n  z-index: 1000;\n  padding-top: 0 !important;\n  padding-bottom: 0 !important;\n  pointer-events: none;\n  width: 100% !important;\n  margin: 0 auto !important;\n\n  .demo-grid {\n    height: 100%;\n  }\n\n  .demo-grid__item {\n    span {\n      background-color: rgba($hanred, 0.25);\n    }\n  }\n}\n\n.demo-grid {\n  @include grid;\n}\n\n.demo-grid__item {\n  @include grid-item(1);\n\n  span {\n    display: block;\n    height: 100%;\n    background-color: blue;\n    color: $white;\n  }\n}\n\n.demo-grid--full {\n  .demo-grid__item {\n    @include grid-item(8);\n    max-width: grid-calc(8);\n  }\n}\n\n.demo-grid--text {\n  .demo-grid__item {\n    @include grid-item(8);\n    max-width: grid-calc(8);\n  }\n}\n\n.demo-grid--video {\n  .demo-grid__item {\n    @include grid-item(10);\n    max-width: grid-calc(10);\n  }\n}\n\n.demo-grid--half {\n  .demo-grid__item {\n    @include grid-item(6);\n  }\n}\n\n.demo-grid--third {\n  .demo-grid__item {\n    @include grid-item(4);\n  }\n}\n\n.demo-grid--quarter {\n  .demo-grid__item {\n    @include grid-item(3);\n  }\n}\n","// Breakpoint mixin\n@mixin breakpoint($first, $second: false) {\n  // One given value will compile to min-width\n  @if length($first) == 1 and $second == false {\n    @media (min-width: $first) {\n      @content;\n    }\n  }\n\n  // Both given values will compile to min-width & max-width\n  @if $first and $second {\n    // Subtract 1px on max-width so queries don't conflict\n    $second: $second - 1px;\n\n    @media (min-width: $first) and (max-width: $second) {\n      @content;\n    }\n  }\n\n  $keyword: nth($first, 1);\n\n  // Parameter 'min' will compile value to min-width\n  @if $keyword == min {\n    $value: nth($first, 2);\n    @media (min-width: $value) {\n      @content;\n    }\n  }\n\n  // Parameter 'max' will compile value to max-width\n  @if $keyword == max {\n    // Collect the actual pix value from the parameters\n    $value: nth($first, 2);\n\n    // Subtract 1px on max-width so queries don't conflict\n    $value: $value - 1px;\n\n    @media (max-width: $value) {\n      @content;\n    }\n  }\n}\n","/**\n * content only visible for screenreaders\n */\n\n@mixin accessibility {\n  position: absolute;\n  left: -999em;\n  top: -100em;\n  color: white;\n  background-color: $black;\n  font-weight: normal;\n  height: 0;\n}\n\n.accessibility {\n  @include accessibility;\n}\n\n/**\n * Focus outline\n */\n\n@mixin focus-outline($color: $black, $width: 1px, $offset: 3px) {\n  outline: $width solid rgba($color, 0.75);\n  outline-offset: $offset;\n}\n\n/**\n * Focus shadow\n * only used when outline isn't pretty\n */\n\n@mixin focus-shadow($color: $black, $width: 1px) {\n  box-shadow: 0 0 0 $width rgba($color, 0.75);\n}\n\n/**\n * Focusable element\n * adds focus outline for mouse-focus only (see track-focus.js)\n */\n\n@mixin focusable($color: $black, $width: 1px, $offset: 3px) {\n  outline: 0;\n\n  [data-whatinput=\"keyboard\"] & {\n    &:focus {\n      @include focus-outline($color, $width, $offset);\n      @content;\n    }\n  }\n}\n","// remove pointer events for Edge\n// https://stackoverflow.com/questions/24078524/svg-click-events-not-firing-bubbling-when-using-use-element\n\nuse {\n  pointer-events: none;\n}\n",".demo-typo {\n  @include grid;\n  display: block;\n}\n\n.demo-typo__bigtitle {\n  @include bigtitle;\n  @include margin-bottom-small;\n\n  text-transform: uppercase;\n  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);\n}\n\n.demo-typo__eyebrow {\n  @include eyebrow;\n  @include margin-bottom-standard;\n}\n\n.demo-typo__title {\n  @include h2;\n  @include margin-bottom-small;\n\n  @include breakpoint($bp-m) {\n    @include underscore;\n  }\n}\n\n@mixin demo-section-padding {\n  padding-top: 50px;\n  padding-bottom: 50px;\n}\n\n.demo-typo__intro {\n  @include lead;\n  @include margin-bottom-standard;\n}\n\n.demo-typo__content {\n  @include base;\n  @include margin-bottom-large;\n}\n\n.demo-typo__cards {\n  @include grid;\n}\n\n.demo-typo__cards__card {\n  @include breakpoint($bp-s) {\n    @include grid-item(12);\n  }\n\n  @include breakpoint($bp-m) {\n    @include grid-item(6);\n  }\n}\n\n.demo-typo__cards__card__title {\n  @include title3;\n  @include margin-bottom-small;\n}\n\n.demo-typo__cards__card__content {\n  @include base;\n  @include margin-bottom-large;\n}\n","@mixin bigtitle {\n  font-family: $bold-condensed;\n  font-size: 28px;\n  color: $white;\n  text-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\n\n  @include breakpoint($bp-m) {\n    font-size: 45px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 61px;\n  }\n}\n\n@mixin h1 {\n  @include margin-bottom-standard;\n  font-family: $bold-condensed;\n  font-size: 28px;\n  text-transform: uppercase;\n\n  @include breakpoint($bp-m) {\n    font-size: 38px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 48px;\n  }\n}\n\n@mixin h2 {\n  font-family: $bold-condensed;\n  font-size: 24px;\n  text-transform: uppercase;\n\n  @include breakpoint($bp-m) {\n    font-size: 29px;\n  }\n\n  @include breakpoint($bp-xl) {\n    font-size: 34px;\n  }\n}\n\n@mixin h3 {\n  font-family: $bold-condensed;\n  font-size: 20px;\n  text-transform: uppercase;\n\n  @include breakpoint($bp-m) {\n    font-size: 22px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 24px;\n  }\n}\n\n@mixin h4 {\n  font-family: $bold-condensed;\n  font-size: 18px;\n  text-transform: uppercase;\n\n  @include breakpoint($bp-m) {\n    font-size: 20px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 21px;\n  }\n}\n\n@mixin h5 {\n  font-family: $bold-condensed;\n  font-size: 15px;\n  text-transform: uppercase;\n\n  @include breakpoint($bp-m) {\n    font-size: 18px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 19px;\n  }\n}\n\n@mixin title1 {\n  @include h1;\n  text-transform: none;\n}\n\n@mixin title2 {\n  @include h2;\n  text-transform: none;\n}\n\n@mixin title3 {\n  @include h3;\n  text-transform: none;\n}\n\n@mixin title4 {\n  @include h4;\n  text-transform: none;\n}\n\n@mixin title5 {\n  @include h5;\n  text-transform: none;\n}\n\n@mixin eyebrow {\n  font-family: $bold-condensed;\n  font-size: 15px;\n  color: $hanred;\n  text-transform: uppercase;\n\n  @include breakpoint($bp-m) {\n    font-size: 17px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 18px;\n  }\n}\n\n@mixin lead {\n  font-family: $demi;\n  font-size: 18px;\n  line-height: 1.6;\n\n  @include breakpoint($bp-m) {\n    font-size: 22px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 24px;\n  }\n}\n\n// Also used for ul/ol\n@mixin p {\n  @include margin-bottom-standard;\n  font-family: $medium;\n  font-size: 16px;\n  line-height: 1.6;\n\n  @include breakpoint($bp-m) {\n    font-size: 18px;\n    line-height: 1.7;\n  }\n}\n\n@mixin base {\n  font-family: $medium;\n  font-size: 16px;\n  line-height: 1.7;\n}\n\n/** \n* Underscore can be used in titles\n*/\n\n@mixin underscore {\n  &::after {\n    color: $hanred;\n    content: \"_\";\n  }\n}\n\n/**\n* Normal links\n*/\n\n@mixin link {\n  color: $hanred;\n  text-decoration: underline;\n\n  &:hover,\n  &:focus {\n    text-decoration: none;\n  }\n}\n","@mixin margin-bottom-standard {\n  margin-bottom: 1em;\n}\n\n@mixin margin-bottom-small {\n  margin-bottom: 0.5em;\n}\n\n@mixin margin-bottom-large {\n  margin-bottom: 2em;\n}\n",".nav {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  width: 100%;\n  z-index: 100;\n  transition: transform 0.35s $ease;\n  backface-visibility: hidden;\n  -webkit-backface-visibility: hidden;\n\n  @mixin hide-topbar {\n    transform: translateY(-48px);\n\n    @include breakpoint($bp-m) {\n      transform: translateY(-88px);\n    }\n  }\n\n  .is-scrolled-free & {\n    @include hide-topbar;\n  }\n\n  .is-scrolled-to-top & {\n    transform: translateY(0);\n  }\n\n  .is-scrolled-to-bottom & {\n    transform: translateY(0);\n  }\n\n  .is-scrolling-up & {\n    transform: translateY(0);\n  }\n\n  .modal-is-open--coursenav & {\n    @include hide-topbar;\n    bottom: 0;\n    height: 100%;\n  }\n}\n\n/**\n * Nav spacer, real time spacing is done by javascript\n */\n.nav-spacer {\n  height: 48px;\n  max-height: 165px;\n\n  @include breakpoint($bp-m) {\n    height: 87px;\n  }\n}\n","@mixin coursenav-title {\n  @include focusable($black);\n  @include underscore;\n  align-self: center;\n  text-decoration: none;\n  color: $black;\n  text-transform: uppercase;\n  font-family: $bold-condensed;\n  font-size: 18px;\n  line-height: 1;\n\n  @include breakpoint($bp-m) {\n    font-size: 22px;\n  }\n\n  @include breakpoint($bp-l) {\n    font-size: 21px;\n  }\n}\n\n/**\n * Close overlay\n */\n\n@keyframes animate-coursenav-overlay-in {\n  to {\n    opacity: 0.15;\n  }\n}\n\n@keyframes animate-coursenav-overlay-out {\n  to {\n    opacity: 0;\n  }\n}\n\n.coursenav-overlay {\n  background-color: $black;\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  height: calc(100vh + 250px); // add some for scroll up logic\n  width: 100vw;\n  opacity: 0;\n  z-index: 97;\n  transform: scale(0);\n\n  .modal-is-open--coursenav &,\n  .has-coursenav-dropdown-open & {\n    animation: animate-coursenav-overlay-in 2.5s $ease forwards;\n    transform: scale(1, 1, 1);\n  }\n}\n","@include breakpoint($bp-l) {\n  @keyframes animate-coursenav-out-desktop {\n    to {\n      height: auto;\n    }\n  }\n\n  .coursenav {\n    @include page-spacing;\n    position: relative;\n    z-index: 98;\n    background-color: $grey1;\n    border-bottom: 1px solid $grey2;\n\n    &.is-closed {\n      animation: animate-coursenav-out-desktop 0 forwards; // easiest fix for providing an onanimationend event for modal.js\n    }\n  }\n\n  .coursenav-wrapper {\n    @include grid;\n  }\n\n  .coursenav-body-wrapper {\n    @include grid-item(12);\n    display: flex;\n  }\n\n  /**\n  * Title\n  */\n\n  .coursenav__title {\n    @include coursenav-title;\n    display: block;\n  }\n\n  /**\n  * Links\n  */\n\n  .coursenav__links {\n    display: flex;\n    align-self: center;\n    margin-left: auto;\n  }\n\n  .coursenav__links__item {\n    position: relative;\n\n    &.has-focus {\n      .coursenav__links__item__subnav {\n        transform: scale(1, 1, 1);\n\n        &::after {\n          display: block;\n        }\n      }\n    }\n\n    &:last-of-type {\n      .coursenav__links__item__subnav {\n        left: auto;\n        right: 0;\n      }\n    }\n  }\n\n  .coursenav__links__item__link {\n    @include focusable($grey3, 1px, -1px);\n    display: block;\n    padding: 27px 24px;\n    border-bottom: 5px solid transparent;\n    color: $black;\n    text-decoration: none;\n    font-size: 15px;\n    transition: border-color 0.15s, background-color 0.15s;\n\n    .has-focus & {\n      background-color: $white;\n      border-bottom-color: $grey2;\n    }\n\n    .is-active & {\n      border-bottom-color: $hanred;\n      background-color: $white;\n      font-family: $demi;\n    }\n\n    .icon-arrow {\n      display: none;\n    }\n  }\n\n  /**\n  * Dropdown\n  */\n\n  .coursenav__links__item__subnav {\n    transform: scale(0);\n    position: absolute;\n    left: 0;\n    width: 235px;\n\n    &::after {\n      z-index: 1;\n      content: \"\";\n      display: none;\n      position: absolute;\n      top: 0;\n      right: -15px;\n      bottom: -15px;\n      left: -15px;\n      background-color: transparent;\n    }\n  }\n\n  .coursenav-dropdown {\n    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);\n  }\n\n  .coursenav-dropdown__item {\n    border-bottom: 1px solid $grey2;\n    z-index: 2;\n    position: relative;\n\n    &:last-of-type {\n      border-bottom: 0;\n    }\n  }\n\n  .coursenav-dropdown__item__link {\n    @include focusable($grey3, 1px, 0);\n    font-size: 14px;\n    color: $grey5;\n    background-color: $white;\n    padding: 24px;\n    display: block;\n    transition: background-color 0.15s;\n    text-decoration: none;\n\n    &:hover {\n      background-color: $grey2;\n      text-decoration: underline;\n    }\n\n    &.is-inview {\n      font-family: $bold;\n    }\n  }\n\n  /**\n   * Mobile toggle\n   */\n\n  .coursenav__toggle {\n    display: none;\n  }\n}\n","/**\n * use data instead of breakpoint to avoid nested media queries\n * which causes issues in IE11\n * \n * the use of the data-breakpoint things breaks usage of\n * the `.selector &`-rule, so you'll have to work around that\n */\n\n// sass-lint:disable force-pseudo-nesting\nhtml:not([data-breakpoint=\"desktop\"]) {\n  @keyframes animate-coursenav-in-mobile {\n    to {\n      height: 100%;\n    }\n  }\n\n  @keyframes animate-coursenav-out-mobile {\n    to {\n      height: 56px;\n    }\n  }\n\n  @keyframes animate-coursenav-in-tablet {\n    to {\n      height: 100%;\n    }\n  }\n\n  @keyframes animate-coursenav-out-tablet {\n    to {\n      height: 80px;\n    }\n  }\n\n  .coursenav {\n    background-color: $white;\n    position: relative;\n    z-index: 98;\n    height: 56px;\n    display: flex;\n    flex-direction: column;\n    padding-left: 0;\n    padding-right: 0;\n\n    @include breakpoint($bp-m) {\n      height: 80px;\n      background-color: transparent;\n      pointer-events: none;\n    }\n\n    &.is-open {\n      animation: animate-coursenav-in-mobile 0.4s $ease forwards;\n\n      @include breakpoint($bp-m) {\n        max-height: calc(100% - 80px);\n        animation: animate-coursenav-in-tablet 1s $ease forwards;\n      }\n    }\n\n    &.is-closed {\n      height: 100%;\n      animation: animate-coursenav-out-mobile 0.4s $ease forwards;\n\n      @include breakpoint($bp-m) {\n        animation: animate-coursenav-out-tablet 0.4s $ease forwards;\n      }\n    }\n  }\n\n  .coursenav-wrapper {\n    @include pretty-scrollbar($hanred);\n    overflow-y: auto;\n\n    @include breakpoint($bp-m) {\n      border-top: 1px solid $grey4;\n      background-color: $white;\n      pointer-events: auto;\n    }\n  }\n\n  .coursenav-body-wrapper {\n    height: 100%;\n  }\n\n  /**\n  * Title\n  */\n\n  .coursenav__title {\n    display: none;\n  }\n\n  /**\n  * Links\n  */\n\n  .coursenav__links {\n    @include breakpoint($bp-m) {\n      display: flex;\n      width: 100%;\n      height: 100%;\n    }\n  }\n\n  .coursenav__links__item {\n    position: relative;\n    border-bottom: 1px solid $grey2;\n    padding: 30px;\n    display: block;\n\n    &:last-of-type {\n      border-bottom: 0;\n    }\n\n    &.is-active {\n      background-color: $grey1;\n\n      .coursenav__links__item__link {\n        font-family: $demi;\n\n        .icon-arrow {\n          display: none;\n        }\n\n        // red border below active link\n        &::after {\n          position: absolute;\n          border: 2px solid $hanred;\n          content: \"\";\n          left: 0;\n          right: 0;\n          display: block;\n          bottom: 0;\n        }\n      }\n\n      .coursenav__links__item__subnav {\n        &::before {\n          display: none;\n        }\n      }\n    }\n\n    @include breakpoint($bp-m